.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: #fffdfd;
    height: 100vh;
    width: 350px;
    position: fixed;
    top: 0;
    left: 0;
}
@media screen and (max-width: 768px) {
    .header {
        display: none;
    }
}